import React, {Component} from 'react';
import {  Route, Link } from "react-router-dom";
import Topic from './Topic';
import { Container, Row, Col } from 'reactstrap'; //LAYOUT
class Recipes extends Component{


render(){


return (
  <Container>
         <Row>
           <Col>
  <div>
    <h2>Recipe's</h2>
    <ul>
      <li>
        <Link to={`${this.props.match.url}/magaritaPizza`}>Magarita Pizza</Link>
      </li>
      <li>
        <Link to={`${this.props.match.url}/hawainPizza`}>Hawain Pizza</Link>
      </li>
      <li>
        <Link to={`${this.props.match.url}/mexicanPizza`}>Mexican Pizza</Link>
      </li>
    </ul>

    <Route path={`${this.props.match.url}/:topicId`} component={Topic} />
    <Route
      exact
      path={this.props.match.url}
      render={() => <h3>Please select a topic.</h3>}
    />
  </div>
</Col>
</Row>
</Container>
);



}



}
export default Recipes;



