import React from 'react';

import { Container, Row, Col } from 'reactstrap'; //LAYOUT
import { Button, Form, FormGroup, Label, Input } from 'reactstrap'; //order


class Order extends React.Component{
  constructor(props){
    super(props);
  this.state ={

  orderName: ' ',
  orderStreet:' ',
  orderTwnCity:' ',
  orderPcode:' ',
  orderPhone:' ',
  orderQty:' ',
  orderNotes:' ',
}
}

  onNameChange = (event) =>{
  console.log(event.target.value);
this.setState({ orderName:event.target.value});
  }

  onStreetChange = (event) =>{
  console.log(event.target.value);
this.setState({ orderStreet:event.target.value});
  }
  onTwnCityChange = (event) =>{
  console.log(event.target.value);
this.setState({ orderTwnCity:event.target.value});
  }
  onPcodeChange = (event) =>{
  console.log(event.target.value);
this.setState({ orderPcode:event.target.value});
  }

  onPhoneChange = (event) =>{
  console.log(event.target.value);
this.setState({ orderPhone:event.target.value});
  }

  onQtyChange = (event) =>{
  console.log(event.target.value);
this.setState({ orderQty:event.target.value});
  }

  onNotesChange = (event) =>{
  console.log(event.target.value);
this.setState({ orderNotes:event.target.value});
  }

onSubmitOrder = () =>{
console.log(this.state.orderName);
console.log(this.state.orderStreet);
console.log(this.state.orderTwnCity);
console.log(this.state.orderPcode);
console.log(this.state.orderPhone);
console.log(this.state.orderQty);
console.log(this.state.orderNotes);

}


render(){
return(

  <div>
<Container>
<Row>
<Col xs="12" sm="12">
    <h2>Place Your Order</h2>
  </Col>
</Row>


<Row>
<Col xs="12" sm="12">
     <Form>

       <FormGroup row>
         <Label for="name" sm={2}>Your Name</Label>
         <Col sm={10}>
           <Input
             type="text"
             name="name"
             id="name"
             placeholder="Your Name"
             onKeyUp={this.onNameChange}
           />
         </Col>
       </FormGroup>


       <FormGroup row>
         <Label for="street" sm={2}>Street</Label>
         <Col sm={10}>
           <Input
              type="text"
              name="street"
              id="street"
              placeholder="Your Street"
             onKeyUp={this.onStreetChange}
            />
         </Col>
       </FormGroup>

       <FormGroup row>
         <Label for="twn_city" sm={2}>Town/City</Label>
         <Col sm={10}>
           <Input
             type="text"
             name="twn_city"
              id="twn_city"
              placeholder="Town/City"
              onKeyUp={this.onTwnCityChange}
            />
         </Col>
       </FormGroup>


       <FormGroup row>
         <Label for="pcode" sm={2}>Post Code</Label>
         <Col sm={10}>
           <Input
             type="text"
             name="pcode"
             id="pcode"
             placeholder="Post Code"
            onKeyUp={this.onPcodeChange}
            />
         </Col>
       </FormGroup>


       <FormGroup row>
         <Label for="phone" sm={2}>Your Phone</Label>
         <Col sm={10}>
           <Input
             type="phone"
             name="phone"
              id="phone"
              placeholder="Your Phone"
             onKeyUp={this.onPhoneChange}
            />
         </Col>
       </FormGroup>



       <FormGroup row>
         <Label for="email" sm={2}>Email</Label>
         <Col sm={10}>
           <Input
             type="email"
             name="email"
             id="email"
             placeholder="your@email.com"
            onKeyUp={this.onEmailChange}
           />
         </Col>
       </FormGroup>

       <FormGroup row>
          <Label for="qty" sm={2}>How many bases would you like?</Label>
          <Col sm={10}>
          <Input
            type="select"
            name="qty"
            id="qty"
            onChange ={this.onQtyChange}
              >
            <option value='1'>1</option>
            <option value='2'>2</option>
            <option value='3'>3</option>
            <option value='4'>4</option>
            <option value='5'>5</option>
          </Input>
        </Col>
        </FormGroup>


       <FormGroup row>
         <Label for="notes" sm={2}>Text Area</Label>
         <Col sm={10}>
           <Input
             type="textarea"
             name="notes"
              id="notes"
             onKeyUp={this.onNotesChange}
             />
         </Col>
       </FormGroup>


       <FormGroup check row>
         <Col sm={{ size: 10, offset: 2 }}>
           <Button
             className="btn-primary"
             onClick={this.onSubmitOrder}
             >Place Order</Button>
         </Col>
       </FormGroup>
     </Form>
   </Col>
 </Row>

   </Container>
  </div>

);


}
}
export default Order;



