import React from 'react';
import { Container, Row, Col } from 'reactstrap'; //LAYOUT

const About = () =>{

return(

  <div>


    <Container>
           <Row>
             <Col>  <h2>About</h2></Col>
           </Row>


           <Row>
             <Col xs="12" sm="12">
               Hand rolled Pizza bases easy to prepare in any oven creating a taste experience of a wood fired pizza.
             </Col>
           </Row>
   </Container>



  </div>

);

}

export default About;



