import React from 'react';
import { Jumbotron } from 'reactstrap'; //Home
import { Card, CardTitle, CardImg, CardImgOverlay } from 'reactstrap'; //home
import { Container, Row, Col } from 'reactstrap'; //LAYOU
import { Link } from "react-router-dom";
import pizza from '../pizza.png';

const Home = () =>{

return(

  <div>
      <div>
         <Jumbotron fluid style={{backgroundColor:'black'}}>
           <Container fluid >
             <h1 className="display-3" style={{color:'red', fontSize:'10vh'}}>Pezzo Di Cielo</h1>
             <p className="lead"  style={{ color:'red'}}>Artisan Pizza Bases</p>
           </Container>
         </Jumbotron>
       </div>

       <Container>

              <Row>
                 <Col xs="6" sm="4">
  <Link to={`/recipes/magaritaPizza`}>
                    <Card inverse>
                       <CardImg width="100%" src={pizza} alt="Card image cap" />
                       <CardImgOverlay style={{height:'15%',width:'100%', backgroundColor:'black',color:'red'}}>
                         <CardTitle>Magarita</CardTitle>

                       </CardImgOverlay>
                     </Card>
</Link>
                   </Col>

                  <Col xs="6" sm="4">
  <Link to={`/recipes/hawainPizza`}>
                     <Card inverse>
                       <CardImg width="100%" src={pizza} alt="Card image cap" />
                           <CardImgOverlay style={{height:'15%',width:'100%', backgroundColor:'black', color:'red'}}>
                           <CardTitle>Hawain</CardTitle>
                       </CardImgOverlay>
                     </Card>
</Link>
                   </Col>


                     <Col xs="6" sm="4">
  <Link to={`/recipes/mexicanPizza`}>
                           <Card inverse>
                           <CardImg width="100%" src={pizza} alt="Card image cap" />
                           <CardImgOverlay style={{height:'15%',width:'100%', backgroundColor:'black', color:'red'}}>
                             <CardTitle>Mexican</CardTitle>
                           </CardImgOverlay>
                         </Card>
</Link>
                       </Col>

               </Row>


      </Container>
    </div>

);

}

export default Home;



