import React from 'react';
import {pizza_recipes} from '../pizza_recipes.js';



const Topic = ({match}) =>{


  const recip = pizza_recipes.map((val,i) => {
    if( val.type === match.params.topicId){
  return (
  <div key={i}>
     <h1>{val.name}</h1>
   <div>{val.method}</div>


   </div>
  );
  }
  });


return(
  <div>
{recip}
  </div>
);
}

export default Topic;



