import React, { Component } from 'react';
import Home from './components/Home'
import About from './components/About'
import Order from './components/Order'
import Recipes from './components/Recipes'


import './App.css';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem } from 'reactstrap';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import { Container, Row, Col } from 'reactstrap'; //LAYOU

import { SocialIcon } from 'react-social-icons';
//import {recipes} from './recipes.js';

class App extends Component {
  constructor(props) {
     super(props);
     //this.state ={
     //recipes: recipes,
     //}


     this.toggleNavbar = this.toggleNavbar.bind(this);
     this.state = {
       collapsed: true
     };
   }

   toggleNavbar() {
     this.setState({
       collapsed: !this.state.collapsed
     });
   }









  render() {
    return (
      //<div className="App">
      //   <header className="App-header">
      //     <img src={logo} className="App-logo" alt="logo" />
      //     <h1 className="App-title">Welcome to React</h1>
      //   </header>
      //   <p className="App-intro">
      //     To get started, edit <code>src/App.js</code> and save to reload.
      //   </p>
      // </div>

<div className="App">

<div style={{flex:'1 0 auto',width:'100%',height:'100%'}}>
      <Router>
        <div>

        <div>
                <Navbar color="dark" dark>
                  <NavbarBrand href="/home" className="mr-auto"  style={{ color:'red'}}>Pezzo Di Cielo</NavbarBrand>
                  <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
                  <Collapse isOpen={!this.state.collapsed} navbar>
                    <Nav navbar>
                      <NavItem>
                        <Link  to="/pezzo-di-cielo/home" style={{fontFamily:'sans-serif', color:'red', textDecoration:'none'}}>Home</Link>
                      </NavItem>
                      <NavItem>
                        <Link to="/pezzo-di-cielo/about" style={{fontFamily:'sans-serif', color:'red', textDecoration:'none'}}>About</Link>
                      </NavItem>
                      <NavItem>
                        <Link to="/pezzo-di-cielo/recipes" style={{fontFamily:'sans-serif', color:'red', textDecoration:'none'}}>Recipe's</Link>
                      </NavItem>
                      <NavItem>
                        <Link to="/pezzo-di-cielo/order" style={{fontFamily:'sans-serif', color:'red', textDecoration:'none'}}>Order</Link>
                      </NavItem>
                    </Nav>
                  </Collapse>
                </Navbar>
              </div>


            <Route exact path="/pezzo-di-cielo/home" component={Home} />
          <Route path="/pezzo-di-cielo/about" component={About} />
          <Route path="/pezzo-di-cielo/recipes" component={Recipes} />
          <Route path="/pezzo-di-cielo/order" component={Order} />
        </div>

      </Router>

{window.location.pathname === '/pezzo-di-cielo/'? <Home/> : null }





</div>

<div style={{display:'flex', flexFlow:'column', marginTop:'100px',width:'100%',height:'150px', backgroundColor:'#0f0f0f'}}>

  <Container >

    <Row style={{paddingTop:'50px'}}>
      <Col><SocialIcon url="https://web.facebook.com/pg/Pezzodicielope" /></Col>
    </Row>
         <Row style={{paddingTop:'30px'}}>
           <Col style={{ color:'red'}}>Pezzo Di Cielo &copy; 2018 All rights reserved </Col>
         </Row>
 </Container>




</div>

</div>


    );
  }
}












export default App;



